.all_heart {
  display: inline-block;
  width: 300px;
  height: 350px;
  position: relative;
}
.all_heart:before,
.all_heart:after {
  position: absolute;
  content: "";
  width: 100px;
  height: 150px;
  top: 110px;
  left: 50%;
  background-color: #fbcfcf;
  border-radius: 120px 120px 0 0;
  transform: rotate(-45deg);
  transform-origin: 0 100%;
}
.all_heart:after {
  transform: rotate(45deg);
  transform-origin: 100% 100%;
  left: 50px;
}
@media screen and (max-width: 350px) {
  .all_heart {
    width: 230px;
    height: 300px;
  }
  .all_heart:before,
  .all_heart:after {
    top: 80px;
  }
  .all_heart:after {
    left: 15px;
  }
}
