  transform-origin: 50% 50%;
  animation: heart_anim_10 0.7s ease infinite alternate;
}
.heart10 .heart:after,
.heart10 .heart:before {
  animation: heart_grad 0.7s linear infinite alternate;
}
.heart10 .all_heart:after,
.heart10 .all_heart:before {
  animation: heart_grad 0.7s linear infinite alternate;
}
.clearfix {
  *zoom: 1;
}
.clearfix:before,
.clearfix:after {
  content: ' ';
  display: table;
}
.clearfix:after {
  clear: both;
}
.hover {
  cursor: pointer;
}
.hover:hover {
  opacity: 0.8;
}
@media screen and (max-width: 740px) {
  .hover:hover {
    opacity: 1;
  }
}
